html, body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  /* main color sections */
  --primary: #2980B9;
  --primary-hover: #0091AD;
  --secondary: #D62839;
  --secondary-hover: #F03A47;
  --default-light: #f5f5f5;
  --default-light-variant: #cecece;
  --default-dark: #061826;
  --default-dark-variant: #595e61;
  --danger: #DD0426;
  --danger-hover: #F02D3A;

  /* color variation */
  --gray-primary: #D3D3D3;
  --gray-silver: #C0C0C0;
  --gray-dark: #A9A9A9;

  /* Typography color section */
  --tags: #36827F;
  --tags2: #9B287B;
  --text-primary: #424342;

  /* shadows color section */
  --light-shadow: #bbbbbb;
  --shadow-bottom-light: 1px 1px 4px var(--light-shadow);

  /* Edges shaping section */
  --circle: 50px;
}

@import "components/header/header.css";
