.slick-slider{
    overflow: hidden;
}

.slick-slide{
    margin: 0;
    padding: 0;
}

.slide{
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 60vh;
    filter: brightness(40%);
}

.banner-area{
    position: relative;
    height: 400px;
}

.banner-area .slider{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    height: 100%;
    z-index: -1;
}

.slider-overlay .banner-info{
    color: var(--gray-primary);
}

.slider-overlay .banner-info h1{
    font-size: 5vmax;
    margin-block-start: 0;
    margin-block-end: 0;
}

.slider-overlay .banner-info h4{
    margin-block-start: 0;
    margin-block-end: 0;
}

.slider-overlay{
    margin: auto;
    width: 80%;
    height: 100%;
    text-align: center;
    z-index: 10;
}

.slider-overlay .overlay-container{
    width: 100%;
    display: flex;
    align-items: center;
    text-align: left;
}

.slider-overlay .overlay-container h4{
    color: var(--gray-primary);
}

.slider-overlay .select-container{
    width: 100%;
    padding: 0 2rem;
}

.slider-overlay .select{
    width: 100%;
}

